"use client"
import React, { useEffect, useState } from 'react';
import Link from 'next/link';
import './favourite.css'
import Cookies from 'js-cookie';
import { GuitarCard } from '#/entities/guitar';

interface FavoriteItem {
  id: string;
  imageUrl: string;
  category: string;
  title: string;
  price: number;
}

// Функция для получения данных товаров по ID (замените на реальный запрос к вашему API)
async function fetchItemsByIds(ids: string[]): Promise<FavoriteItem[]> {
  try {
    // Пример реального запроса (замените на ваш API endpoint)
    const response = await fetch(`https://673876654eb22e24fca800c5.mockapi.io/:items`);
    if (!response.ok) {
      throw new Error('Ошибка при загрузке товаров');
    }
    return await response.json();
  } catch (error) {
    console.error('Ошибка при загрузке товаров:', error);
    return [];
  }
}

export function FavoritesPage() {
  const [favorites, setFavorites] = useState<FavoriteItem[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const favoritesIds = Cookies.get('favorites') 
      ? JSON.parse(Cookies.get('favorites') as string) 
      : [];

    const fetchFavorites = async () => {
      try {
        if (favoritesIds.length > 0) {
          const items = await fetchItemsByIds(favoritesIds);
          setFavorites(items);
        } else {
          setFavorites([]);
        }
      } catch (error) {
        console.error('Ошибка при загрузке избранного:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchFavorites();
  }, []);

  const removeFromFavorites = (id: string) => {
    const updatedFavorites = favorites.filter(item => item.id !== id);
    setFavorites(updatedFavorites);
    
    const favoritesIds = updatedFavorites.map(item => item.id);
    Cookies.set('favorites', JSON.stringify(favoritesIds), { expires: 365 });
  };

  if (loading) {
    return <div className="container">Загрузка...</div>;
  }

  return (
    <div className="container">
      <h1>Избранные товары</h1>
      
      {favorites.length === 0 ? (
        <div className="empty-favorites">
          <p>В избранном пока нет товаров</p>
          <Link href="/catalog">
            <button className="btn-primary">Перейти в каталог</button>
          </Link>
        </div>
      ) : (
        <div className="favorites-grid">
          {favorites.map(item => (
            <div key={item.id} className="favorite-item">
              <GuitarCard 
                id={item.id}
                imageUrl={item.imageUrl}
                category={item.category}
                title={item.title}
                price={item.price}
                onFavoriteToggle={() => removeFromFavorites(item.id)}
              />
            </div>
          ))}
        </div>
      )}
    </div>
  );
}